@media all and (orientation: portrait) {
  .container.indexBox {
    background: #3961b0 url("../images/main-bg-2.jpg") center bottom no-repeat !important;
    background-size: 100% 100% !important;
  }
}

@media all and (orientation: landscape) {
  .container.indexBox {
    background: #3961b0 url("../images/main-bg-x.jpg") center bottom no-repeat !important;
    background-size: 100% 100% !important;
    padding-top: 30px !important;
  }
  .container.indexBox .logo {
    width: 35% !important;
  }
  .container.indexBox .slogan {
    width: 50% !important;
    margin: 0 auto !important;
    margin-top: 22px !important;
    margin-bottom: 10px !important;
  }
  .container.indexBox .slogan img {
    width: 100%;
  }
  .container.indexBox .slogan-sub {
    font-size: 12px;
    text-align: center;
    color: #fff;
    padding: 0 120px !important;
    text-shadow: 0px 1px 1px #333;
  }
  .container.indexBox .linklist {
    bottom: 70px !important;
    left: 130px !important;
    right: 130px !important;
  }
  .container.indexBox .linklist ul {
    box-sizing: border-box;
    display: flex;
  }
  .container.indexBox .linklist ul li i.icon {
    display: block;
    width: 50px;
    height: 50px;
    background-color: #fff;
    border-radius: 50%;
    margin: 0 auto;
    margin-bottom: 5px;
  }
  .container.indexBox .linklist ul li span {
    display: block;
    text-align: center;
    color: #fff;
    font-weight: 500;
    font-size: 16px;
  }
  .container.indexBox .copyright {
    position: absolute;
    bottom: 30px;
    font-size: 12px;
    text-align: center;
    color: #fff;
    left: 0;
    right: 0;
  }
}

